import React from 'react'
import {Link} from 'react-router-dom'
export const Footer = () => {
    return (
        <footer>
            <div >
                <p className="d-flex justify-content-center" style={{marginTop:'5%'}}>Copyright &copy; 2021</p>
                <Link to="/about"className="d-flex justify-content-center" style={{marginTop:'1%',color:'#48D1CC'}}>About</Link>
            </div>
        </footer>
    )
}
export default Footer